@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap');

@import url('https://fonts.googleapis.com/icon?family=Material+Icons');

$padWidth: 85px;
$padHeight: 80px;

.container {
  i{
  padding: 5px;
  }
  h2{
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    top: 40px;
    text-align: center;
    color: #909090;
  }
  a{
    text-decoration: none;
  }
}

body {
  user-select: none;
  background-image: linear-gradient(to bottom, #FFFFFF, #eecccc);
  font-family: Roboto; 
}

#root {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Roboto;
  height: 100vh;
}

.inner-container  {
  display:flex;
  position: relative;
  min-height: 335px; 
  background-color: white;
  text-align: center;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 18px;
  width: 660px;
  box-shadow: inset 12px 12px 48px #F1F8FF;
  box-shadow: inset -12px -12px 24px #ea9999;
  filter: drop-shadow(12px 12px 24px #e36a36); 
  border-radius: 30px;
  .pad-bank {
    width: $padWidth * 3 + 30;
    height: $padHeight * 3 + 30;
    display: inline-block;
    margin: 25px 25px 40px 40px ;
    .drum-pad {
      position: relative;
      float: left;
      height: $padHeight;
      width: $padWidth;
      border-radius: 10px;
      margin-right: 10px;
      padding-top: 30px;
      box-sizing: border-box;
      cursor: pointer;
    }
  }
  
.controls-container {
  display: inline-block;
  position: relative;
  height: $padHeight * 3 + 32;
  width: 258px;
  margin: 0px 25px 30px 20px;
  vertical-align: top;
  .control {
    width: 100px;
    margin: auto;
      p {
        margin-bottom: 0;
      }
      .select {
        @extend .outer-select;
        .inner {
          @extend .inner-select;
        }
      }
    }
    #display {
      width: 70%;
      background-color: white;
      margin: 25px auto;
      padding: 10px;
      box-sizing: border-box;
      border-radius: 5px;
      border: 1px solid grey;
    }
  }
}

.outer-select {
  margin: auto;
  width: 60px;
  height: 20px;
  padding: 1px;
  background: pink;
  border-radius: 10px;
  margin-top: 10px;
}

.inner-select {
  position: relative;
  width: 32px;
  height: 32px;
  background: red;
  box-sizing: border-box;
  border-radius: 16px;
  cursor: pointer;
  bottom: 6px;
  right: -2px;
}

.volume-slider{
  position: center;
  #lowvol{
    padding: 0px 15px 0px 15px;
  }
  #highvol{
    padding: 0px 15px 0px 15px;
  }
}

.control-bank {
   position: center;
  border: 2px solid grey;
  border-radius: 5px;
  margin-top: 15px;
  width: 93%;
}

.bank-button1 {
  padding: 17px;
  background-color: white;
  background-size: cover;
  border: none;
  cursor: pointer;
  font-size: 14px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}
.bank-button1:hover{
    background-color: #FFBFCB;
    
  }

.bank-button2 {
  padding: 17px;
  background-color: white;
  border: none;
  border-left: 1px solid grey;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  cursor: pointer;
  font-size: 14px;
}
.bank-button2:hover{
    background-color: #FFBFCB;
  };

@media screen and (max-width: 550px) {
  .inner-container {
    transform: scale(0.7);
    flex-direction: column;
   
  }
  .controls-container {
    margin-bottom: 20px;
  }
}

$track-color: pink !default;
$thumb-color: red !default;

$thumb-radius: 14px !default;
$thumb-height: 28px !default;
$thumb-width: 28px !default;
$thumb-shadow-size: 0px !default;
$thumb-shadow-blur: 0px !default;
$thumb-shadow-color: #111 !default;
$thumb-border-width: 0 !default;
$thumb-border-color: #fff !default;

$track-width: 58% !default;
$track-height: 5px !default;
$track-shadow-size: 0px !default;
$track-shadow-blur: 0px !default;
$track-shadow-color: #222 !default;
$track-border-width: 0px !default;
$track-border-color: #000 !default;

$track-radius: 0 !default;
$contrast: 5% !default;

@mixin shadow($shadow-size, $shadow-blur, $shadow-color) {
  box-shadow: $shadow-size $shadow-size $shadow-blur $shadow-color,
    0 0 $shadow-size lighten($shadow-color, 5%);
}

@mixin track() {
  width: $track-width;
  height: $track-height;
  cursor: pointer;
  transition: all 0.2s ease;
  margin-bottom: 4px;
}

@mixin thumb() {
  @include shadow($thumb-shadow-size, $thumb-shadow-blur, $thumb-shadow-color);
  border: $thumb-border-width solid $thumb-border-color;
  height: $thumb-height;
  width: $thumb-width;
  border-radius: $thumb-radius;
  background: $thumb-color;
  cursor: pointer;
}

[type='range'] {
  -webkit-appearance: none;
  margin: calc($thumb-height / 2) 0;
  width: $track-width;

  &:focus {
    outline: none;
  }

  &::-webkit-slider-runnable-track {
    @include track();
    @include shadow(
      $track-shadow-size,
      $track-shadow-blur,
      $track-shadow-color
    );
    background: $track-color;
    border: none;
    border-radius: $track-radius;

  }

  &::-webkit-slider-thumb {
    @include thumb();
    -webkit-appearance: none;
    margin-top: calc($track-border-width * -2 + ($track-height / 2) -
      ($thumb-height / 2));
  }

  &:focus::-webkit-slider-runnable-track {
    background: lighten($track-color, $contrast);
  }

  &::-moz-range-track {
    @include track();
    @include shadow(
      $track-shadow-size,
      $track-shadow-blur,
      $track-shadow-color
    );
    background: $track-color;
    border: $track-border-width solid $track-border-color;
    border-radius: $track-radius;
  }

  &::-moz-range-thumb {
    @include thumb();
  }

  &::-ms-track {
    @include track();
    background: transparent;
    border-color: transparent;
    border-width: $thumb-width 0;
    color: transparent;
  }

  &::-ms-fill-lower {
    @include shadow(
      $track-shadow-size,
      $track-shadow-blur,
      $track-shadow-color
    );
    background: darken($track-color, $contrast);
    border: $track-border-width solid $track-border-color;
    border-radius: $track-radius * 2;
  }

  &::-ms-fill-upper {
    @include shadow(
      $track-shadow-size,
      $track-shadow-blur,
      $track-shadow-color
    );
    background: $track-color;
    border: $track-border-width solid $track-border-color;
    border-radius: $track-radius * 2;
  }

  &::-ms-thumb {
    @include thumb();
  }

  &:focus::-ms-fill-lower {
    background: $track-color;
  }

  &:focus::-ms-fill-upper {
    background: lighten($track-color, $contrast);
  }
}